import axios from 'axios';

export function InterceptorDemo2() {
  axios.interceptors.response.use((res)=>{
    return res;
  },(err) => {
    alert('Error From Interceptor.')
    if(err.response.status === 404)
    {
      throw new Error(`${err.config.url} Not Found` );
    }
    throw err;
  });
}
