import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'

export default function ProductDetails() {

    console.log("Call.......");

    const [myProductInfo, setProductInfo] = useState({});

   const {id} = useParams();

   const fetchData = async () => {
    let productInfo = await axios.get(`https://fakestoreapi.com/products/${id}`);
    
    setProductInfo(productInfo.data);
   }

   useEffect(()=>{
    fetchData();
   }, [id]);


  return (
    <>
      <img src={myProductInfo.image} alt="img" width="500px" />
    </>
  )
}
