import React from 'react'
import { Link, Outlet, useNavigate } from 'react-router-dom';
//import { Link } from 'react-router-dom'

export default function AboutUs() {

  const myNavigation = useNavigate();

  let redirect = () =>{
      alert("Hi.....");
      myNavigation('/about-inner');
  }

  return (
    <>
      <h1>I am About Us</h1>
      {/* <Link to="/about-inner" >
        <button>About Inner</button>
      </Link> */}


      
        <button onClick={redirect}>About Inner</button>
        <button onClick={ ()=>myNavigation(-1)}>Prev</button>
        <button onClick={()=>myNavigation(1)}>Next</button>

        <Link to='/about-us/education'>Education</Link>

        <Outlet />
      
    </>
  )
}

    